@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
    margin: 0;
}

body {
    font-family: 'Inter', serif;
    background-color: #fff;
}


h1 {
    font-size: 40px;
    margin: 20px;
    text-align: center;
    color: #333333;
    font-weight: bold;
}

h2 {
    font-size: 30px;
    margin: 20px;
    text-align: center;
    color: #333333;
    font-weight: bold;
}

.login {
    position: relative;
    top: 20%;
    margin: auto;
    text-align: center;
    width: 650px;
}

.login a {
    color:  #00669f;
    text-decoration: none;
}

.entrada {
    width: 600px;
    margin: 10px;
}

.menu {
    width: 624px;
    margin: 10px;
    height: 45px;
    font-size: 15px;
    padding: 10px;
    border: 2px solid #333333;
}

.entrar {
    background-color: #2166cc;
    border-radius: 20px;
    width: 280px;
    height: 45px;
    margin: 20px;
    border: 0;
    color: #fff;
}

.resetear {
    background-color: #888888;
    color: #fff;
    border-radius: 20px;
    width: 280px;
    height: 45px;
    margin: 20px;
    border: 0;
}

input[type=text], input[type=number], input[type=password] {
    font-size: 15px;
    padding: 10px;
    border: 2px solid #333333;
}

input[type=button], input[type=reset] {
    font-size: 15px;
    font-weight: bold;
}

* {
    box-sizing: border-box;
    margin: 0;
}



.table-container {
    display: flex;
    width: 20%;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgb(62, 111, 201);
    border-radius: 15px;
    margin: 40px auto;
    padding: 10px 15px;
}

.table-bordered {
    margin: 40px auto;
    padding: 10px 15px;
    width: 40%;
    background-color: rgb(205, 214, 231);
    border-radius: 5px;
}

.table-bordered thead {
    background-color: rgb(98, 120, 165);
}
